body{
   
    .fixed{
        position: fixed;
    }
    .message-body{
        background: deepskyblue;
    }
    .left-panel{
        min-height: 653px;
        span{
            color: white;
        }
        .logo{
            margin-left: -40px;
        }
        background: lightslategray;
    }
    .account-info{
        margin-right: 120px;
        min-height: 653px;
        min-width: 250px;
        background:lightskyblue;
        span{
            color: white;
        }
        .user-details{
            
            a,p{
                color: white;
            }
            .user-name{
            margin-left: 40px;
            }
            .user-img{
                margin-left: 60px;
            }
            background: skyblue;
        }
        .tab-content{
            .shared-link{
                height: 150px;
                width: 135px;
                .shared_count{
                    margin-left: 60px;
                }
                p,span:first-child{
                    margin-left: 45px;
                    color: white;
                }
            }
        }
    }
    .temporal-push-top{
        margin-top: -600px;
    }
    
    .upload-tpls{
        .upload-indicator{
            margin-left: -140px;
        }
        width: 800px;
        .positioning{
            margin-left:145px;z-index:15000;
        }
        .browse-wrap{
            margin-left: 145px;
            width: 100%;
        }
        .upload-drop-zone {
                        height: 150px;
                        border-width: 2px;
                        width:400px;
                        margin-bottom: 20px;
                    }
                    /* skin.css Style*/
                    .upload-drop-zone {
                        color: #ccc;
                        border-style: dashed;
                        border-color: #ccc;
                        line-height: 200px;
                        text-align: center
                    }
                    .upload-drop-zone.drop {
                        color: #222;
                        border-color: #222;
                    }
                    input.upload {
                        right:0;
                        margin:0;
                        bottom:0;
                        padding:0;
                        opacity:0;
                        outline:none;
                        cursor:inherit;
                        position:absolute;
                        font-size:1000px !important;
                        }
                        span.upload-path {
                        text-align: center;
                        margin:20px;
                        display:block;
                        font-size: 80%;
                        color:#3b5998;
                        font-weight:bold;
                        font-family:tahoma, arial, sans-serif;
                    }
    }
    
    /**sass for common dialog*/
    md-toolbar.md-default-theme {
      background-color: #FFFFFF;
      color: #fff;
    }
    
    .md-icon-button {
      color: #ccc;
    }
    
    .md-button.md-default-theme:not([disabled]) {
      background-color: #BDC0CE;
    }
    .logout{

        margin-left:300px;
    }
}